import React from 'react';
import { Row, Col } from 'antd';


const Header = () => {
  return (
    <Row justify="center" style={{ backgroundColor: '#f0f2f5', padding: '20px 0', marginBottom: '20px' }}>
      <Col span={24}>
        <h1 style={{ textAlign: 'center', margin: 0 }}>List of Posts</h1>
      </Col>
    </Row>

  );
};

export default Header;
